type EventHandler = (e: Event, pos: { x: number, y: number }) => void;

export const addScrollEventListener = (handler: EventHandler) => {
    let last_known_y = 0;
    let last_known_x = 0;
    let ticking = false;

    window.addEventListener('scroll', function (e) {
        last_known_y = window.scrollY;
        last_known_x = window.scrollX;

        if (!ticking) {
            window.requestAnimationFrame(function () {
                handler(e, { y: last_known_y, x: last_known_x });
                ticking = false;
            });

            ticking = true;
        }
    });
}